___CSS_LOADER_EXPORT___.push([module.id, `* {
  padding: 0;
  margin: 0;
  font-family: Verdana, Arial;
}

body {
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.page-header {
  padding: 2rem;

  display: grid;
  place-items: center;
  gap: 1rem;

  background-color: #66bfbf;
}

.title {
  font-size: 3rem;
}

#location,
#get-weather,
#units-button {
  padding: 0.5rem;

  border: none;
  background-color: #eaf6f6;
}

#location:focus {
  outline: solid 2px lightgrey;
}

#get-weather:active,
#units-button:active {
  background-color: #dbf0f0;
}

.error {
  display: none;

  padding: 0.25rem;

  color: darkred;
  background-color: white;
}

.error.active {
  display: block;
}

.content {
  background-color: #eaf6f6;
  flex: 1;
  display: flex;
  justify-content: center;
}

.loading-icon-container {
  padding: 5rem;
  place-self: start;

  display: none;
  flex-direction: column;
  align-items: center;
}

.loading-icon-container.active {
  display: flex;
}

#loading {
  height: 20rem;
  width: auto;
}

.loading-icon-container a {
  font-size: 0.75rem;
}

.forecast {
  margin-top: 3rem;
  height: min-content;
  padding: 3rem;

  background-color: white;

  display: none;
  grid-template-rows: 2rem 1fr;
  grid-template-columns: 1fr 1fr;
}

.forecast.active {
  display: grid;
}

.weather-header {
  grid-area: 1 / 1 / 2 / 3;

  font-size: 1.5rem;
}

.weather-data {
  grid-area: 2 / 1 / 3 / 2;

  padding: 2rem;

  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.weather-data div {
  display: flex;
}

.conditions {
  font-size: 1.25rem;
}

.gap {
  flex: 1;
  margin-right: 2rem;
}

hr {
  height: 2px;
  margin: 0;
  position: relative;
  bottom: 0.35rem;

  border: none;
  background-color: lightgrey;
}

.icon-container {
  display: grid;
  place-items: center;
}

.icon {
  width: 10rem;
  height: auto;
}
